.modal-main_container {
  width: 100%;
  height: 100%;
  max-height: 100vh;
  background-color: var(--white);
  z-index: 999;
  visibility: hidden;
  flex-direction: column;
  align-items: center;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  box-shadow: 0 0 8.78px rgba(0, 0, 0, .01), 0 0 18.17px rgba(0, 0, 0, .02), 0 0 36.24px rgba(0, 0, 0, .03), 0 0 130.6px rgba(0, 0, 0, .04), 0 0 223px rgba(0, 0, 0, .05);
}

.modal-main_container.is-open {
  visibility: initial;
}

.btn-close-modal {
  width: 20px;
  height: 20px;
  background-color: var(--white);
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  padding: 0;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.btn-close-icon {
  width: 20px;
  height: 20px;
  fill: var(--black);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-close-modal:hover .btn-close-icon, .btn-close-modal:focus .btn-close-icon {
  fill: red;
}

.modal-item-image_container {
  width: 276px;
  height: 325px;
  justify-content: center;
  align-items: center;
  margin: 40px auto 0;
  display: flex;
}

.modal-item-image {
  width: 276px;
  height: 274px;
}

.modal-item-title_container {
  margin: 14px auto 0;
}

.modal-item-title {
  letter-spacing: -.02em;
  color: var(--black);
  font-size: 14px;
  line-height: 1.28;
}

.modal-item-price {
  letter-spacing: -.02em;
  color: rgba(20, 20, 20, .5);
  font-size: 14px;
  line-height: 1.28;
}

.modal-form {
  width: 295px;
  height: 578px;
  margin-top: 40px;
}

.modal-form-element_container, .modal-form-comment_container {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.modal-form-element_container {
  margin-bottom: 18px;
}

.modal-form-comment_container {
  margin-bottom: 20px;
}

.modal-form-label {
  letter-spacing: -.02em;
  color: var(--black);
  margin: 0 0 12px;
  font-size: 12px;
  line-height: 1.167;
}

.modal-form-input {
  width: 295px;
  height: 42px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(20, 20, 20, .5);
  border-radius: 8px;
  outline: rgba(0, 0, 0, 0);
  padding-left: 16px;
}

.modal-form-input::-ms-input-placeholder {
  letter-spacing: -.02em;
  font-size: 14px;
  line-height: 1.28;
}

.modal-form-input::placeholder {
  letter-spacing: -.02em;
  font-size: 14px;
  line-height: 1.28;
}

.modal-form-comment {
  width: 295px;
  height: 170px;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(20, 20, 20, .5);
  border-radius: 8px;
  outline: rgba(0, 0, 0, 0);
  padding-top: 12px;
  padding-left: 16px;
}

.modal-form-comment::-ms-input-placeholder {
  letter-spacing: -.02em;
  font-size: 14px;
  line-height: 1.28;
}

.modal-form-comment::placeholder {
  letter-spacing: -.02em;
  font-size: 14px;
  line-height: 1.28;
}

.modal-form_submit-button {
  width: 295px;
  height: 50px;
  background-color: var(--dark-green);
  text-align: center;
  color: var(--white);
  border: none;
  border-radius: 8px;
  margin-bottom: 12px;
  font-size: 12px;
  line-height: 1.167;
}

.modal-submit_terms {
  letter-spacing: -.02em;
  text-align: left;
  color: rgba(20, 20, 20, .4);
  padding-bottom: 40px;
  font-size: 12px;
  line-height: 1.167;
}

@media (min-width: 768px) {
  .modal-main_container {
    width: 744px;
    height: 670px;
    justify-content: center;
    margin: 0 auto;
    display: flex;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .modal_inner-container {
    flex-direction: row;
    align-items: flex-start;
    gap: 20px;
    margin: 40px;
    padding: 0;
    display: flex;
  }

  .modal-form {
    width: 368px;
    height: 590px;
    margin: 0;
  }

  .modal-form-input, .modal-form-comment, .modal-form_submit-button {
    width: 368px;
  }
}

@media (min-width: 1280px) {
  .modal-main_container {
    width: 904px;
    height: 694px;
  }

  .modal-form {
    width: 508px;
    height: 614px;
  }

  .modal-form-input, .modal-form-comment, .modal-form_submit-button {
    width: 508px;
  }

  .modal-form-element_container {
    margin-bottom: 24px;
  }

  .modal-form-comment_container {
    margin-bottom: 40px;
  }
}

